import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, fetchEncryptStream } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt } from "../../../components";


import "./dashboard.css"
import { CloseModal } from "../../../components/DialogHRM";

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const CheckBox = CheckBox_Cmpt();

const frmNm = "new_product"

const AddProduct  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm);
		ffm["productId"] = gen16CodeId();

		let bdyq2 = {
			query: "addProductsHRG",
			params: {
				businessId:  getStore("profileUser")?.businessId
			},
			form:ffm
		};
		
		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
		
	};





	const clean = (f) => {
		updStore(frmNm, {});
		set_obs(gen16CodeId())
	};

	


	return (
		<div style={{
			maxHeight: "80vh",
			width: "560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Agregar Producto
				</p>
				<div className="flexSpace"/>
				
			</div>

			<TextInput  
				init={getStore(frmNm)?.["name"]} 
				label={"Nombre"} 
				width={520} 
				updChanges={(e)=>updFrm("name",e)}
			/>
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["unit"]} 
					label={"Unidad de medida"} 
					width={120} 
					updChanges={(e)=>updFrm("unit",e)}
				/>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["upc_barcode"]} 
					label={"codigo escanear"} 
					width={120} 
					updChanges={(e)=>updFrm("upc_barcode",e)}
				/>
				<div className="flexSpace"/>
				
			</div>
		

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<div className="card">
					<button onClick={save}>
						Guardar
					</button>
				</div>

			</div>
		

			
		</div>
	);
};

export default AddProduct;